.default {
    border: none;
    box-shadow: 0px 4px 3px -3px rgba(0,0,0,0.36);
    background: #eee7e7;
    outline: none;
    cursor: pointer;
}

.red {
    border: none;
    box-shadow: 0px 4px 3px -3px rgba(0,0,0,0.36);
    outline: none;
    cursor: pointer;
    background: #e92424;
}
